import React from 'react';
import { nanoid } from 'nanoid';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import { useForm } from 'react-hook-form';
import {
  FormStyle,
  Input,
  InputGroup,
  InputLabel,
  Button,
  ErrorMessageStyle,
} from './ContacrForm.styled';
import { phoneRegExp } from 'components/calc/phoneRegExp';
import { toast } from 'react-toastify';
import {
  useAddContactsMutation,
  useFetchContactsQuery,
} from 'redux/Contacts/contactsApi';
import { IoPersonAddOutline } from 'react-icons/io5';
import 'react-toastify/dist/ReactToastify.css';
import { Title } from 'components/App/App.styled';

const schema = yup.object().shape({
  name: yup.string().required("–Ü–º'—è –æ–±–æ–≤'—è–∑–∫–æ–≤–µ!"),
  number: yup
    .string()
    .required("–ù–æ–º–µ—Ä –æ–±–æ–≤'—è–∑–∫–æ–≤–∏–π!")
    .matches(
      phoneRegExp,
      '–ù–æ–º–µ—Ä –º–∞—î —Å–∫–ª–∞–¥–∞—Ç–∏—Å—å —Ç—ñ–ª—å–∫–∏ –∑ —Ü–∏—Ñ—Ä. –ü—Ä–∏–±–µ—Ä—ñ—Ç—å –ª–∏—à–Ω—ñ —Å–∏–º–≤–æ–ª–∏.'
    )
    .min(7, '–ù–æ–º–µ—Ä –º–∞—î –±—É—Ç–∏ –Ω–µ –º–µ–Ω—à–µ 7 —Ü–∏—Ñ—Ä'),
});

const nameId = nanoid();
const numberId = nanoid();

export const ContactForm = () => {
  const { data: contacts = [] } = useFetchContactsQuery();
  const [addContacts] = useAddContactsMutation();

  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm({
    resolver: yupResolver(schema),
  });

  const onSubmit = data => {
    addContact(data.name.trim(), data.number);

    const repeatName = contacts.some(
      el => el.name.toLowerCase() === data.name.trim().toLowerCase()
    );
    const repeatNumber = contacts.some(
      el => el.number.replace(/\D/g, '') === data.number.toLowerCase()
    );
    if (repeatName || repeatNumber) {
      return;
    }

    reset();
  };
  const addContact = (name, number) => {
    const repeatName = contacts.some(
      el => el.name.toLowerCase() === name.toLowerCase()
    );
    const repeatNumber = contacts.some(
      el => el.number.replace(/\D/g, '') === number.toLowerCase()
    );

    if (repeatName) {
      return toast.error(
        `${name} –≤–∂–µ —î –≤ –∫–æ–Ω—Ç–∞–∫—Ç–∞—Ö. –°–ø—Ä–æ–±—É–π—Ç–µ –≤–≤–µ—Å—Ç–∏ —ñ–Ω—à–µ —ñ–º'—è`
      );
    } else if (repeatNumber) {
      return toast.error(
        `–¢–∞–∫–∏–π –Ω–æ–º–µ—Ä ${number} –≤–∂–µ —î –≤ –∫–æ–Ω—Ç–∞–∫—Ç–∞—Ö. –°–ø—Ä–æ–±—É–π—Ç–µ –≤–≤–µ—Å—Ç–∏ —ñ–Ω—à–∏–π`
      );
    }
    const contactItem = {
      createdAt: new Date(),
      name,
      number,
      id: nanoid(),
    };
    toast.success('–ù–æ–≤–∏–π –∫–æ–Ω—Ç–∞–∫—Ç –¥–æ–¥–∞–Ω–æ');
    addContacts(contactItem);
  };

  return (
    <FormStyle onSubmit={handleSubmit(onSubmit)}>
      <Title>–°—Ç–≤–æ—Ä–∏—Ç–∏ –∫–æ–Ω—Ç–∞–∫—Ç</Title>
      <InputGroup>
        <InputLabel htmlFor={nameId}>–Ü–º'—è</InputLabel>
        <Input
          {...register('name')}
          type="text"
          name="name"
          title="Name may contain letters and numbers, apostrophe, dash and spaces. For example Adrian, Jacob Mercer2, Charles de Batz de Castelmore d'Artagnan"
          // required
          id={nameId}
        />

        {errors.name && (
          <ErrorMessageStyle>{errors.name?.message}</ErrorMessageStyle>
        )}
      </InputGroup>
      <InputGroup>
        <InputLabel htmlFor={numberId}>–¢–µ–ª–µ—Ñ–æ–Ω</InputLabel>
        <Input
          {...register('number')}
          type="tel"
          name="number"
          title="Phone number must be digits and can contain spaces, dashes, parentheses and can start with +"
          // required
          id={numberId}
        />
        {errors.number && (
          <ErrorMessageStyle>{errors.number?.message}</ErrorMessageStyle>
        )}
      </InputGroup>
      <Button type="submit">
        –î–æ–¥–∞—Ç–∏ –∫–æ–Ω—Ç–∞–∫—Ç
        <IoPersonAddOutline />
      </Button>
    </FormStyle>
  );
};
